html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
}

h1, h2, h3, h4, h5 {
  color: #00252e;
  margin: 0;
  font-family: "Poppins", sans-serif;
}

a, a:visited, a:active {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}

button {
  cursor: pointer;
  font-family: "Open Sans", sans-serif;
  border: none;
  border-radius: 2rem;
}

li {
  list-style: none;
}

.header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5rem;
}
.header__logo {
  width: 30%;
  max-width: 12.5rem;
}
.header__logo img {
  width: 100%;
}
.header button {
  background-color: white;
  padding: 0.25rem 1.4375rem 0.375rem 1.375rem;
  border-radius: 2rem;
  font-size: 0.625rem;
  border: none;
  font-weight: bold;
  color: #00252e;
}
.header button:hover {
  color: #808d99;
}

@media screen and (min-width: 50em) {
  .header {
    max-width: 80rem;
    margin: 0 auto 4.4375rem;
  }
  .header button {
    padding: 0.8125rem 4.0625rem 1rem 3.9375rem;
  }
}
.header_hero_container {
  padding: 1.6875rem 1rem 2.3125rem 1rem;
  background-image: url(/images/bg-hero-mobile.svg);
  background-color: #ebfbff;
}

.hero {
  text-align: center;
  height: 100%;
  padding: 0 1.1875rem 0 1.1875rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.hero__text h1 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}
.hero__text p {
  font-size: 1rem;
  color: #00252e;
  line-height: 1.5rem;
  margin-bottom: 2rem;
}
.hero__text button {
  background: #ff52bf;
  color: white;
  width: 15rem;
  height: 2.5rem;
  margin-bottom: 3.5rem;
}
.hero__text button:hover {
  background: #ff99db;
}
.hero__image img {
  width: 100%;
}

@media screen and (min-width: 50em) {
  .header_hero_container {
    background-image: url(/images/bg-hero-desktop.svg);
    background-color: #ebfbff;
    padding: 4.25rem 5rem 4.5rem 5rem;
  }

  .hero {
    flex-direction: row;
    max-width: 80rem;
    margin: auto;
    padding: 0;
  }
  .hero__text {
    margin-right: 3.375rem;
    text-align: left;
  }
  .hero__text h1 {
    max-width: 33.5rem;
  }
  .hero__text p {
    max-width: 32.5rem;
  }
  .hero__image img {
    max-width: 42.5rem;
  }
}
@media screen and (min-width: 68.75em) {
  .hero__text h1 {
    font-size: 2.5rem;
    line-height: 3.75rem;
  }
  .hero__text p {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
  .hero__text button {
    width: 17.5rem;
    height: 3.5rem;
  }
}
.about {
  text-align: center;
  padding: 3.6875rem 1.25rem 14.3125rem 1.25rem;
}
.about__item {
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.07);
  padding: 0 2.5rem 0 2.5rem;
  border-radius: 1rem;
  height: auto;
}
.about__item img {
  width: 100%;
}
.about__item h3 {
  font-size: 1.25rem;
  margin-bottom: 1rem;
}
.about__item p {
  color: #808d99;
  font-size: 0.875rem;
  line-height: 1.3125rem;
}
.about__item:not(:last-child) {
  margin-bottom: 2.5rem;
}
.about .grow {
  padding-top: 3.5rem;
  padding-bottom: 2.0625rem;
}
.about .grow div:first-child {
  order: 1;
}
.about .grow div:first-child img {
  margin-bottom: 3.375rem;
  width: 93%;
}
.about .flow {
  padding-top: 4.1rem;
  padding-bottom: 3.375rem;
}
.about .flow div:first-child img {
  margin-bottom: 4.1rem;
}
.about .users {
  padding-top: 3.729rem;
  padding-bottom: 2.0625rem;
}
.about .users div:first-child {
  order: 1;
}
.about .users div:first-child img {
  margin-bottom: 3.729rem;
  width: 95%;
}

@media screen and (min-width: 50em) {
  .about__item {
    display: flex;
    flex-direction: row;
    text-align: left;
    align-items: center;
    max-width: 77rem;
    margin: auto;
  }
  .about__item h3 {
    font-size: 1.75rem;
    line-height: 2.625rem;
  }
  .about__item p {
    font-size: 1.125rem;
    line-height: 1.6875rem;
  }
}
@media screen and (min-width: 68.75em) {
  .about .users {
    padding: 3.7875rem 1.61875rem 3.84375rem 7.5rem;
  }
  .about .users div:first-child img {
    display: block;
    margin-bottom: 0;
    max-width: 26.25rem;
    width: 100%;
  }
  .about .users p {
    max-width: 29.8125rem;
    margin-right: 11.1875rem;
  }
  .about .flow {
    padding: 4.42625rem 1.8125rem 4.49875rem 7.5381rem;
  }
  .about .flow div:first-child img {
    display: block;
    margin-bottom: 0;
    max-width: 26.125rem;
    width: 100%;
    margin-right: 10.340625rem;
  }
  .about .flow p {
    max-width: 31.1875rem;
  }
  .about .grow {
    padding: 3.33625rem 2rem 3.33625rem 7.5rem;
  }
  .about .grow div:first-child img {
    margin-bottom: 0;
    width: 100%;
    max-width: 25.375rem;
  }
  .about .grow p {
    margin-right: 11.5625rem;
    max-width: 30.5rem;
  }
}
.footer {
  background: #00252e;
  color: white;
  font-size: 0.875rem;
  padding: 8.6875rem 1rem 2.875rem 1.3125rem;
}
.footer__logo {
  margin-bottom: 1.5625rem;
}
.footer path {
  fill: white;
}
.footer p {
  max-width: 21.1875rem;
  line-height: 1.5rem;
}
.footer small {
  text-align: center;
  display: block;
}
.footer__contact {
  margin-bottom: 2.5625rem;
}
.footer__contact li {
  display: flex;
  align-items: center;
}
.footer__contact li:not(:last-child) {
  margin-bottom: 1.5rem;
}
.footer__contact li img {
  margin-right: 1rem;
}
.footer__nav {
  margin-bottom: 1.5rem;
}
.footer__nav li:not(:last-child) {
  margin-bottom: 1rem;
}
.footer__nav li a:hover {
  border-bottom: 1px white solid;
}
.footer__socials {
  text-align: center;
}
.footer__socials ion-icon {
  font-size: 2rem;
  margin-bottom: 1.5rem;
}
.footer__socials ion-icon:not(:nth-of-type(2)) {
  margin-right: 0.709rem;
}
.footer__socials ion-icon:hover {
  color: #ff52bf;
}

@media screen and (min-width: 50em) {
  .footer {
    padding: 9rem 0rem 4rem 0rem;
  }
  .footer__container {
    max-width: 77.625rem;
    display: flex;
    justify-content: center;
    margin: auto;
  }
  .footer__contact {
    margin-right: 8.125rem;
    margin-bottom: 0;
  }
  .footer__contact li:not(:last-child) {
    margin-bottom: 0.75rem;
  }
  .footer__socials {
    margin-bottom: 0;
    text-align: left;
  }
  .footer__socials ion-icon {
    margin-bottom: 6.6875rem;
  }
  .footer__nav {
    margin-bottom: 0;
    columns: 2;
  }
}
.cta__footer_container {
  position: relative;
}

.cta {
  text-align: center;
  padding: 12px;
  z-index: 5;
  position: absolute;
  top: -7.5rem;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  max-width: 50rem;
}
.cta__container {
  box-shadow: 0px 0px 14px rgba(0, 0, 0, 0.07);
  padding: 2.5625rem 0.75rem;
  border-radius: 1rem;
  background: white;
}
.cta__container h2 {
  font-size: 1.125rem;
  margin-bottom: 1.5rem;
  line-height: 1.875rem;
}
.cta__container button {
  background: #ff52bf;
  color: white;
  width: 12.5rem;
  height: 2.5rem;
  font-size: 0.625rem;
}
.cta__container button:hover {
  background: #ff99db;
}

@media screen and (min-width: 50em) {
  .cta {
    padding: 0;
    top: -11.5rem;
  }
  .cta__container {
    padding: 3.52rem 0 3.25rem 0;
  }
  .cta__container h2 {
    font-size: 2rem;
    line-height: 3.75rem;
    margin-bottom: 2rem;
  }
  .cta__container button {
    font-size: 1.25rem;
    width: 25rem;
    height: 5rem;
  }
}